* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;

}

#main {
  position: relative;
  background: url("https://img.freepik.com/free-vector/white-abstract-background_23-2148810113.jpg?w=2000");
  height: 100vh;
  background-position: center;
  background-repeat: repeat-X;
  background-size: cover;
}

#load {
  position: absolute;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: burlywood;
  background: url("https://img.freepik.com/free-vector/white-abstract-background_23-2148810113.jpg?w=2000");
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.conditionbc {
  flex-direction: column;
}

.container1 {
  width: 100%;
  height: 100vh;
  padding: 100px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.image-class {
  width: "50%";
  display: "flex";
  justify-content: "center";
  align-items: "center"
}

.right-wrapper {
  position: relative;
  width: 100vw;
  /* padding: 10px; */
  height: 5rem;
}

.left-wrapper-outer
{
  /* width: 100vw; */
  height: 80vh;
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  /* padding-top: 20px;
  padding-right: 44px; */
  /* margin: 2rem; */
  /* padding: 1rem; */
}
.right-wrapper-up
{
  height: 7rem;
}

.right-wrapper-up-outer
{

  width: 100vw;
height: 28vh;
  position: absolute;
  bottom: 1px;
  left: 1px;
}
.left-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.game-wrapper {
  margin: 0px;
  padding: 0px;
  height: 328px;

}
/* .mystyle>.square-container>p 
{
  color: black;
} */



.mystyle>.square-container>p {
  position: relative;
  display: inline-block;
  font-size: 90px;
  color: rgb(160, 113, 113);
  text-transform: uppercase;
  animation: flip 1s infinite;
  /* animation-delay:  */
}

@keyframes flip {
  0%,80% {
    transform: rotateY(360deg) 
  }
}

.left-text {
  font-size: 17px;
  position: absolute;
  font-weight: bolder;
  color: #4D6985;
  width: 89%;

}

.conditionC {
  width: 0px;
  height: 0px;
}

conditionB {
  width: 100vh;
  height: 100vw;
}

.res-button-wrapper>.button {
  font-size: 1.2rem;
}

.button {
  margin: 2rem 0rem;
  width: 240px;
  height: 60px;
  border-radius: 40px;
  font-size: 18px;
  color: #60748D;
  background-color: white;
  border: 2px solid #7689A0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  /* font-size: 2rem; */
}

.button:hover {
  background: antiquewhite;
  color: violet;
}

.players {
  display: flex;
  flex-direction: row;
  width: 19rem;

}

.player {
  width: 50%;
  font-size: 18px;
  font-weight: bold;
  padding-bottom: 10px;
  color: #834e4e;
  border-bottom: 4px solid #EFEFEF;

}

.player-X {
  color: #41bed7;
  border-bottom: #41bed7;
}

.player-O {
  color: #9f8ab9;
  border-bottom: #9f8ab9;
}

.game-wrapper {
  width: 307px;
  height: 330px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.winner-wrapper {
  position: relative;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.winner-text {
  font-size: 50px;
  padding: 19px;
  font-weight: bolder;
  color: #4d6985
}

img {
  margin-left: 12px;
  margin-top: 86px;
}

@media only screen and (min-width:1400px) {
  .left-text {
    font-size: 60px;
  }

}
.buttona
{
  display: none;
}
.button-wrapper-in {
  font-size: 1.5rem;
  left: 0;
  right: 0;
  margin-left: "auto";
  margin-right: "auto";
  width: 12rem;
  border: 2px solid grey;
  margin: 1px;
  /* background: yellowgreen; */
  border-radius: 15px;
}

#button-wrapper {
  position: "absolute";
  display: flex;
  justify-content: center;

}

.res-button-wrapper {
  position: "absolute";
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

@media only screen and (max-width:666px) {

  #main {
    height: 146vh;
  }

  .left-wrapper {
    width: 100%;
  }

  .game-wrapper {
    width: 306px;
  }

  .left-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 7px;
    padding: 10px;
  }

  .right-wrapper-up {
    width: 100%;
  }

}

@media only screen and (min-width:783px) {
  .container1 {
    padding: 2px;
  }
}

@media only screen and (max-width:600px) {
  .winner-wrapper {
    width: 100%;
  }

  .left-text {
    width: 100%;
  }

  .right-wrapper {
    width: 100%;
  }

  .image-class {
    width: 100%;
  }

  .right-wrapper-up {
    display: flex;
    flex-direction: column;
  }

}

@media only screen and (max-width:400px) {
  .button {
    margin: 2rem 3rem;
  }

  .button-wrapper-in {
    font-size: 1rem;
  }

  .res-button-wrapper {
    height: 69%;
  }

}

@media only screen and (min-width:200px) {
  .button {
    font-size: 0.7rem;
    margin: 0rem 0rem;
  }
}

@media only screen and (max-width:500px) {
  #main {
    height: 107vh;
  }
  .mystyle>.square-container>p {
    font-size: 70px;
  }

  .game-wrapper {
    width: 14rem;
    height: 13rem;

    margin-right: 0px;
  }

  .players {
    width: 13rem;
  }

  .left-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 7px;
    padding: 10px;
  }

  .right-wrapper {
    width: 100%;
  }

  .winner-wrapper {
    width: 100%;
  }
}